<template>
<v-container>
    <v-text-field
    outlined
    v-model="name"
    label="Enter your Bach Job Name."
    append-icon="mdi-magnify"
    @click:append="search(name)"
    v-on:keyup.enter="search(name)">
  ></v-text-field>
  <v-simple-table>    
    <template v-slot:default>
      <thead>
        <tr bgcolor="lightgrey">
          <th class="text-center">Job ID</th>
          <th class="text-center">Job Name</th>
          <th class="text-center">Parameters</th>
          <th class="text-center">Start Time</th>
          <th class="text-center">End Time</th>
          <th class="text-center">Status</th>    
        </tr>
      </thead>
      <tbody>
        <tr v-for="job in jobs" :key="job.instanceId">
          <td class="text-center">
            <router-link :to="{name: 'history', params: {instanceId: job.instanceId}}">
              {{ job.instanceId }}
            </router-link>
          </td>
          <td class="text-left">{{ job.name }}</td>
          <td class="text-right">{{ job.params }}</td> 
          <td class="text-center">{{ job.execution.startTime }}</td> 
          <td class="text-center">{{ job.execution.endTime }}</td>          
          <td class="text-center">{{ job.execution.status }}</td>    
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  <div class="text-center">
    <v-btn
      small
      block
      :disabled="lastPage"
      @click="findByNameNoOffset(name)"
    >Next</v-btn>
  </div>
</v-container> 
</template>
<script>
import axios from "axios"

export default {
  data () {
    return {
      name: '',
      jobs: [],
      lastInstanceId: '',
      lastPage: false,
      pageSize: 10
    }
  },
  methods: {
    search(name) {
      // 초기화
      this.jobs = []
      this.lastInstanceId = ''
      this.lastPage = false
      this.findByNameNoOffset(name)
    },
    findByNameNoOffset(name) {  
      if(name == '') {
        alert('Enter your Bach Job Name.')
        return
      }
      axios.get(`/job/name?instanceId=${this.lastInstanceId}&name=${name}&pageSize=${this.pageSize}`)
        .then(res => {  
          let dataSize = res.data.length
          // jobs 배열의 재구성
          for(let i=0; i <= dataSize-1; i++) {
            this.jobs.push(res.data[i])
          }    
          // 데이터가 있는경우에만 lastInstanceId 셋팅
          if(dataSize != 0 ) this.lastInstanceId = res.data[dataSize-1].instanceId
          // 마지막 페이지 셋팅
          if(dataSize != this.pageSize || dataSize == 0) this.lastPage = true
        })
        .catch(err => {
          console.error(err)
        })
    }
  }
}
</script>